import React from "react";
import { Select, Form, Typography } from "antd";
import { Icon as LegacyIcon } from "@ant-design/compatible";

const { Text } = Typography;
const CSelectIcon = ({
  id,
  name,
  validate,
  label,
  isRequired,
  onChange,
  options = [],
  value,
  ...rest
}) => {
  return (
    <Form.Item
      label={
        <span>
          {label}
          {isRequired ? (
            <span style={{ marginLeft: "5px", color: "red" }}>*</span>
          ) : null}
        </span>
      }
      name={name}
      {...rest}
      validateStatus={validate ? "error" : null}
      help={validate}
    >
      <Select
        allowClear
        showSearch
        optionFilterProp="children"
        filterOption={(input, option) =>
          (option?.value ?? "").toLowerCase().includes(input.toLowerCase())
        }
        style={{
          width: "100%",
        }}
        placeholder={label}
        defaultValue={value}
        value={value}
        onChange={onChange}
        id={id}
        options={options.map((item, idx) => {
          return {
            label: (
              <>
                <LegacyIcon type={item.label} />
                <Text> {item.label}</Text>
              </>
            ),
            value: item.value,
          };
        })}
      />
    </Form.Item>
  );
};

export default CSelectIcon;
